import {
  AppBar,
  Toolbar,
  Button,
  Divider,
  styled,
  Typography,
  Container,
} from "@mui/material";
import color from "../constants/pallate";
import React from "react";

const StyledDivider = styled(Divider)({
  marginTop: "0.5rem",
  marginBottom: "1.0rem",
  backgroundColor: color.secondary,
  borderBottomWidth: "2px",
});

const Paragraph = styled(Typography)({
  fontFamily: "Rubik",
  fontSize: "1.2rem",
  marginTop: "0.5rem",
  marginBottom: "0.8rem",
});

const Highlight = styled(Typography)({
  color: color.secondary,
  fontWeight: "bold",
  display: "inline",
  fontStyle: "italic",
  fontSize: "1.2rem",
});

const Title = styled(Typography)({
  fontFamily: "Rubik",
  fontWeight: "bold",
  fontStyle: "italic",
  borderBottom: `1px solid ${color.secondary}`,
  display: "inline-block",
  borderWidth: "2px",
  bottomPadding: "0.5rem",
});

const SubTitle = styled(Typography)(({ theme }) => ({
  fontFamily: "Rubik",
  fontWeight: "bold",
  borderLeft: `1px solid ${color.secondary}`,
  leftMargin: "0.5rem",
  paddingLeft: "0.5rem",
  borderWidth: "2px",
  [theme.breakpoints.down("md")]: {
    border: "none",
    leftMargin: "0",
    paddingLeft: "0",
  },
}));

const Navbar = styled(AppBar)({
  marginTop: "1rem", // space from the top
  borderRadius: "10px", // rounded corners
  backgroundColor: color.primary, // background color for the navbar
  maxWidth: "1000px", // limit navbar width to match text content
  marginLeft: "auto",
  marginRight: "auto",
});

const NavbarButton = styled(Button)({
  color: "white",
  fontFamily: "Rubik",
  textTransform: "none", // to keep the button text normal
});

const CustomNavbar = ({ items, name }) => {
  return (
    <Navbar position="static">
      <Toolbar sx={{ justifyContent: "space-between" }}>
        {/* Left side (Name) */}
        <Typography
          variant="h6"
          component="div"
          sx={{ fontFamily: "Rubik", fontWeight: "bold", color: "white" }}
        >
          {name}
        </Typography>

        {/* Right side (Navbar Items) */}
        <div>
          {items.map((item, index) => (
            <NavbarButton key={index} href={item.href}>
              {item.text}
            </NavbarButton>
          ))}
        </div>
      </Toolbar>
    </Navbar>
  );
};

export { StyledDivider, Paragraph, Highlight, Title, SubTitle, CustomNavbar };
